.articleContainer {
    display: flex;
    margin: 50px;
}

img {
    margin: auto;
}

.textSection {
    margin: 10px 20px;
}

.textSection span {
    color: gray;
}

.textSection h2 {
    margin: 0;
    margin-top: 5px;
}

.textSection .readMore {
    color: gray;
    text-decoration: underline;
    cursor: pointer;
}

.textSection .readMore:hover {
    color: lightblue;
}